<template>
  <v-app dark>
    <background></background>
    <v-toolbar app fixed flat>
      <v-toolbar-title>
        {{ title }}
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-tooltip bottom>
          <v-btn flat :disabled='contract.address.length === 0' :href='contractCodeUrl' target='_blank' slot='activator'>
            <font-awesome-icon :icon='["fal", "file-code"]' size='2x'></font-awesome-icon>
          </v-btn>
          <span v-if='contract.address.length > 0'>Contract Code</span>
          <span v-else>Contract unavailable</span>
        </v-tooltip>
        
        <v-menu :close-on-content-click='false' attach='.bonuses-menu' class='bonuses-menu' :nudge-bottom='64'>
          <v-tooltip bottom slot='activator' z-index='0'>
            <v-btn flat slot='activator'>
              <v-badge class='bonus-badge' :color='isBonusClaimed || isBonusUnlocked ? `green darken-2` : `red darken-2`'>
                <span slot='badge' v-if='wasPlayer && !isBonusClaimed' class='text-shadow'>
                  !
                </span>
                <font-awesome-icon :icon='["fal", "trophy"]' size='2x'></font-awesome-icon>
              </v-badge>
            </v-btn>
            <span>Bonuses</span>
          </v-tooltip>
          <bonuses></bonuses>
        </v-menu>

        <v-menu :close-on-content-click='false' attach='.account-menu' class='account-menu' left :nudge-bottom='64' min-width='25vw' max-width='25vw' :disabled='!coinbase'>
          <v-tooltip bottom slot='activator' z-index='0'>
            <v-btn flat slot='activator'>
              <v-chip label outline :style='getAvatarColorAttribute(coinbase)' text-color='white'>
                <v-avatar tile >
                  {{ getAddressName(coinbase) === `` ? `...` : getAddressName(coinbase) }}
                </v-avatar>
                <span>
                  {{ fromWei(balance) }}
                  <font-awesome-icon :icon='["fab", "ethereum"]'></font-awesome-icon>
                </span>
              </v-chip>
            </v-btn>
            <span>Account</span>
          </v-tooltip>
          <account></account>
        </v-menu>
      </v-toolbar-items>
    </v-toolbar>
    <v-content>
      <home></home>
    </v-content>
    <v-footer fixed app v-if='$vuetify.breakpoint.mdAndUp'>
      <span class='ml-3'>
        Made with
        <font-awesome-icon :icon='["fas", "heart"]' size='lg' class='red--text text--darken-2'></font-awesome-icon>
        by <a class='white--text' href='https://twitter.com/EtherButton' target='_blank'>EtherButton</a>
      </span>
    </v-footer>

    <notification></notification>
  </v-app>
</template>

<script>
import store from './store/index';
import { mapActions, mapState, mapMutations, mapGetters } from 'vuex';
import Bonuses from './components/Bonuses.vue';
import Account from './components/Account.vue';
import Background from './components/Background.vue';
import Home from './components/Home.vue';
import Notification from './components/Notification.vue';

export default {
  name: 'App',
  store,

  data() {
    return {
      nowTimerIntervalId: 0,
      refreshStateTimerIntervalId: 0,
      clickSubscription: null,
      playerPayoutSubscription: null
    };
  },

  computed: {
    ...mapState([
      'encryption',
      'coinbase',
      'balance',
      'contract',
      'web3',
      'snackbar'
    ]),
    ...mapGetters([
      'fromWei',
      'getAvatarColorAttribute',
      'getAddressName',
      'getClickEventDetails',
      'getSendPaymentEventDetails',
      'parseClickEvent',
      'getHistoryItem',
      'isRoundOver',
      'isBonusUnlocked',
      'wasPlayer',
      'isBonusClaimed'
    ]),
    contractCodeUrl() {
      return `https://etherscan.io/address/${this.contract.address}#code`;
    },
    title() {
      return `EtherButton • Round ${this.contract.currentRound.id}`;
    }
  },

  components: {
    Bonuses,
    Account,
    Background,
    Home,
    Notification
  },

  watch: {
    async isRoundOver(isRoundOver, wasRoundOver) {
      if (isRoundOver && !wasRoundOver) {
        await this.getRounds();
      }
    }
  },

  async created() {
    try {
      if (process.env.NODE_ENV === 'production' && this.encryption !== 'A79A1D6F4E3F7C2E26219ED7F0EC998253531B6275CE260808AF715CD182EC89') {
        return;
      }

      await this.getWeb3();
      await this.getContract();

      // Keep a timer, accurate to within a second, running for the page.
      // This helps for detecting when the round has expired and the UI should update
      // as well as assists the round timer in updating.
      this.nowTimerIntervalId = setInterval(() => { this.setNow(); }, 500);

      this.refreshStateTimerIntervalId = setInterval(async () => {
        if (this.web3.instance() !== null) {
          await this.getNetworkType();
          await this.getAccountDetails();
        }
      }, 2000);

      // If the contract has not yet been deployed - exit early since no work to be done.
      if (!this.contract.wsInstance) {
        console.warn('Contract not defined. Skipping event subscription setup.');
        return;
      }

      const blockNumber = await this.web3.wsInstance().eth.getBlockNumber();
      this.clickSubscription = this.contract.wsInstance().events.LogClick();
      this.clickSubscription.on('error', console.error);

      this.clickSubscription.on('data', (event) => {
        this.setRoundState(this.parseClickEvent(event));
      });
      // Grab a maximum of two days worth of logs.
      // A block is mined every 14 seconds and there are 86400 seconds in a day.
      const maxBlockCount = 12342;
      const fromBlock = Math.max(0, blockNumber - maxBlockCount);

      this.playerPayoutSubscription = this.contract.wsInstance().events.LogPlayerPayout({ fromBlock });
      this.playerPayoutSubscription.on('error', console.error);

      this.playerPayoutSubscription.on('data', (event) => {
        this.addHistoryItem(this.getHistoryItem(event));
      });
    } catch (error) {
      console.error(`Failed to initialize app. Reason:`, error);
    }
  },

  async beforeDestroy() {
    clearInterval(this.nowTimerIntervalId);

    if (this.clickSubscription) {
      await this.unsubscribeSubscription(this.clickSubscription);
      this.clickSubscription = null;
    }

    if (this.playerPayoutSubscription) {
      await this.unsubscribeSubscription(this.playerPayoutSubscription);
      this.playerPayoutSubscription = null;
    }
  },

  methods: {
    ...mapActions([
      'getWeb3',
      'getContract',
      'getAccountDetails',
      'getNetworkType',
      'getRounds'
    ]),
    ...mapMutations({
      'setNow': 'SET_NOW',
      'addHistoryItem': 'ADD_HISTORY_ITEM',
      'setRoundState': 'SET_ROUND_STATE'
    }),
    unsubscribeSubscription(subscription) {
      return new Promise(resolve => {
        subscription.unsubscribe(resolve);
      });
    }
  }
};
</script>

<style>
body {
  user-select: none;
  overflow: hidden;
}

.application.theme--dark {
	background: radial-gradient(ellipse at center, #212121 0%, #141414   100%) !important;
}

.application.theme--dark .list,
.application.theme--dark .card,
.application.theme--dark .tooltip__content {
  background-color: #212121 !important;
}

.application.theme--dark .toolbar {
  background: linear-gradient(to top, transparent, rgba(20, 20, 20, .66)) !important;;
}

.application.theme--dark .footer {
  background: linear-gradient(to bottom, transparent, rgba(20, 20, 20, .66)) !important;;
}

.application.theme--dark .avatar {
  background-color: var(--avatar-color);
  text-transform: uppercase;
  text-shadow: 0 1px 2px rgba(0, 0, 0, .6);
  height: 40px !important;
  width: 40px !important;
  transition: background-color .3s cubic-bezier(0.25, 0.8, 0.5, 1);
  border-radius: 2px;
}

.application.theme--dark .chip {
  font-size: 16px;
  border-color: var(--avatar-color) !important;
  height: 40px;
  /* I've placed the chip inside of a button and want the cursor to look correct */
  pointer-events: none;
}

.text-shadow {
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
}
</style>

<style scoped>
.bonus-badge {
  transition: background-color .3s cubic-bezier(0.25, 0.8, 0.5, 1);
}
</style>


