<template>
  <v-card class='account'>
    <v-card-text>
      <h3>Address</h3>
      <span class='subheading'>{{ coinbase }}</span>
      <v-slider @input='updateExtraClickSafeguardCount' :value="extraClickSafeguardCount" thumb-label min='0' max='3' step='1' ticks hint='Send enough extra ETH to guarantee a successful click even if (0-3) other clicks are processed simultaneously. Any ETH above what is needed to process your click is immediately refunded.' persistent-hint label='Extra Click Safeguard'></v-slider>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
  computed: {
    ...mapState([
      'coinbase',
      'extraClickSafeguardCount'
    ])
  },

  methods: {
    ...mapMutations({
      'setExtraClickSafeguardCount': 'SET_EXTRA_CLICK_SAFEGUARD_COUNT'
    }),

    updateExtraClickSafeguardCount(value) {
      this.setExtraClickSafeguardCount(value);
    }
  }
};
</script>

<style scoped>
.account {
  border-top: 3px solid #6200EA;
}
</style>



