<template>
  <div>
    <section>
      <introduction></introduction>
      <div class='rect-container elevation-24'>
        <div class='rect-container-top'>      
          <div class='rect1'></div>
          <div class='rect2'></div>
          <div class='rect3'></div>
          <div class='rect4'></div>
          <div class='rect5'></div>
        </div>

        <div class='rect-container-bottom'>      
          <div class='rect1'></div>
          <div class='rect2'></div>
          <div class='rect3'></div>
          <div class='rect4'></div>
          <div class='rect5'></div>
        </div>
      </div>
    </section>
    <section class='elevated mt-5'>
      <about></about>
    </section>
    <section class='elevated' v-if='$store.state.contract.address.length'>
      <contract></contract>
    </section>
    <section class='elevated caption text-xs-center'>
      <p class='text-shadow px-3'>
        Oh! You scrolled all the way to the bottom. Impressive. Have a gold star.
      </p>
      <v-tooltip bottom>
        <v-btn flat large slot='activator' @click='$vuetify.goTo(0)'>
          <font-awesome-icon size='2x' :icon='["fas", "star"]' class='amber--text'></font-awesome-icon>
        </v-btn>
        <span>
          Back to Top
        </span>
      </v-tooltip>
    </section>
  </div>
</template>

<script>
import Introduction from './Introduction.vue';
import History from './History.vue';
import Contract from './Contract.vue';
import About from './About.vue';

export default {
  name: `home`,

  components: {
    Introduction,
    History,
    Contract,
    About
  }

};
</script>

<style scoped>
.elevated {
  /* This helps with the interaction with the background splash art */
  position: relative;
}

.rect-container {
  padding: 0;
  top: 97vh;
  background: linear-gradient(90deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
  background-size: 400% 400%;
  animation: Gradient 15s ease infinite;
  width: 100%;
  height: 120vh;
  transform: skewY(-5deg);
  position: absolute;
  overflow-Y: hidden;
  filter: brightness(.85);
}

.rect-container-top {
  position: absolute;
  top: 0;
  width: 100%;
}

.rect-container-bottom {
  position: absolute;
  bottom: 0;
  width: 100%;
  transform: rotate(180deg);
}

.rect1 {
  width: 30vw;
  background: #212121;
  opacity: 0.1;
  height: 5vh;
  float: left;
}

.rect2 {
  width: 20vw;
  background: #212121;
  opacity: 0.2;
  height: 5vh;
  float: left;
  clear: left;
}

.rect3 {
  width: 20vw;
  background: #212121;
  opacity: 0.5;
  height: 5vh;
  float: right;
}

.rect4 {
  width: 30vw;
  background: #212121;
  opacity: 0.1;
  height: 5vh;
  float: right;
  clear: right;
}

@keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}
</style>


