// TODO: Can I make this an import?
/* eslint-disable */ 
require('vuetify/src/stylus/app.styl');

// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';

import {
  Vuetify,
  VApp,
  VToolbar,
  VTooltip,
  VBadge,
  VFooter,
  VList,
  VBtn,
  VGrid,
  VMenu,
  VChip,
  VAvatar,
  VCard,
  VDivider,
  VSlider,
  VAlert,
  VSubheader,
  VSnackbar
} from 'vuetify';

import 'vuetify/dist/vuetify.min.css';
import fontAwesome from '@fortawesome/fontawesome';
import FontAwesomeIcon from '@fortawesome/vue-fontawesome';
import { faEthereum } from '@fortawesome/fontawesome-free-brands';
import { faClock, faCheck, faLockAlt, faLockOpenAlt, faFileCode, faMousePointer, faRepeat1, faStarExclamation, faTrophy } from '@fortawesome/fontawesome-pro-light';
import { faStar, faHeart } from '@fortawesome/fontawesome-free-solid';

Vue.use(Vuetify, {
  components: {
    VApp,
    VToolbar,
    VTooltip,
    VBadge,
    VFooter,
    VList,
    VBtn,
    VGrid,
    VMenu,
    VChip,
    VAvatar,
    VCard,
    VDivider,
    VSlider,
    VAlert,
    VSubheader,
    VSnackbar
  }
});

Vue.config.productionTip = false;

fontAwesome.library.add(faEthereum, faClock, faCheck, faLockAlt, faLockOpenAlt, faFileCode, faStar, faMousePointer, faRepeat1, faStarExclamation, faHeart, faTrophy);

// Register the font awesome icon component so that it's usable throughout the application
// without explicit, per-module registration.
Vue.component('font-awesome-icon', FontAwesomeIcon);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>'
});



