<template>
  <div class='bonuses'>
    <v-list two-line >
      <v-list-tile>
        <v-list-tile-content>
           <v-list-tile-title>Next Bonus</v-list-tile-title>
           <v-list-tile-sub-title>
             {{ contract.currentRound.playerClickCount }}/{{ contract.currentRound.clickCount }} of {{ fromWei(contract.currentRound.totalBonus) }} ETH
           </v-list-tile-sub-title>
           <v-list-tile-sub-title class='text-xs-right' v-if='isActivePlayer'>
             + {{ fromWei(contract.initialPrice) }} ETH
           </v-list-tile-sub-title>
        </v-list-tile-content>
        <v-list-tile-action>
          <font-awesome-icon size='lg' :icon='["fal", "clock"]' class='yellow--text text--darken-2'></font-awesome-icon>
        </v-list-tile-action>
      </v-list-tile>
      <v-divider inset></v-divider>
      <v-list-tile>
        <v-list-tile-content>
           <v-list-tile-title>Bonus</v-list-tile-title>
           <v-list-tile-sub-title>
             {{ contract.previousRound.playerClickCount }}/{{ contract.previousRound.clickCount }} of {{ fromWei(contract.previousRound.totalBonus) }} ETH
           </v-list-tile-sub-title>
           <v-list-tile-sub-title class='text-xs-right' v-if='wasActivePlayer'>
             + {{ fromWei(contract.initialPrice) }} ETH
           </v-list-tile-sub-title>
        </v-list-tile-content>
        <v-list-tile-action>
          <font-awesome-icon size='lg' :icon='["fal", "check"]' class='green--text text--darken-2' v-if='contract.previousRound.isBonusClaimed'></font-awesome-icon>
          <font-awesome-icon size='lg' :icon='["fal", "lock-open-alt"]' class='green--text text--darken-2' v-else-if='isBonusUnlocked'></font-awesome-icon>
          <font-awesome-icon size='lg' :icon='["fal", "lock-alt"]' class='red--text text--darken-2' v-else></font-awesome-icon>
        </v-list-tile-action>
      </v-list-tile>
    </v-list>
    <v-divider></v-divider>
    <v-btn block large depressed :disabled='!isBonusUnlocked' class='claim' @click='sendTx' color='green darken-2'>
      Claim
    </v-btn>
  </div>
</template>

<script>
import { mapActions, mapState, mapGetters } from 'vuex';

export default {
  name: 'bonuses',

  state() {
    return {
      isSubmitting: false
    };
  },

  computed: {
    ...mapState([
      'coinbase',
      'contract'
    ]),
    ...mapGetters([
      'fromWei',
      'isBonusUnlocked',
      'wasActivePlayer',
      'isActivePlayer'
    ])
  },

  methods: {
    ...mapActions([
      'claimBonus'
    ]),
    async sendTx() {
      if (!this.isBonusUnlocked) return false;

      this.isSubmitting = true;

      try {
        await this.claimBonus();
        this.isSubmitting = false;
      } catch (error) {
        this.isSubmitting = false;
      }
    }
  }
};
</script>

<style scoped>
.bonuses {  
  border-top: 3px solid #6200EA;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
}

.claim {
  /* TODO: A lot of these properties tie w/ Vue-provided styling and then lose when building for production */
  height: 44px !important;   
  border-top-right-radius: 0;
  border-top-left-radius: 0;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
}
</style>


