
import { utils } from 'web3';

export default {
  warnings(state, getters) {
    const warnings = [];

    if (!getters.isMainnet) {
      warnings.push('You are not connected to the mainnet');
    }

    const balance = new utils.BN(state.balance);
    if (state.extraClickSafeguardCount > 0 && getters.extraClickPrice.gt(balance)) {
      warnings.push(`Your balance is low. Extra-click safeguard is throttled`);
    }

    return warnings;
  },
  errors(state, getters) {
    // Get a couple of obviously fatal errors out of the way before handling general cases.
    // This is so I don't have to check if MM is installed and if user is logged in repeatedly.
    if (!getters.isMetaMaskInstalled) {
      return ['MetaMask not found'];
    }

    if (!state.coinbase) {
      return ['You are logged out of MetaMask'];
    }

    // Need to call `toLowerCase` on 'owner' because `getCoinbase` returns an address without a checksum which
    // is represented as an all-lowercase address, but querying the contract returns a property address with capital letters.
    const errors = [];
    if (state.contract.owner.toLowerCase() === state.coinbase) {
      errors.push('You are the contract owner');
    }

    const priceBN = new utils.BN(state.contract.currentRound.price);
    const balanceBN = new utils.BN(state.balance);

    if (priceBN.gt(balanceBN)) {
      errors.push('Your balance is low');
    }

    return errors;
  },
  extraClickPrice(state) {
    // TODO: It would be better to pull the 8.5% from the contract.
    const priceBN = new utils.BN(state.contract.currentRound.price);

    if (state.extraClickSafeguardCount > 0) {
      const numerator = new utils.BN(state.extraClickSafeguardCount * 85);
      const denominator = new utils.BN(1000);
      return priceBN.add(priceBN.mul(numerator).div(denominator));
    } else {
      return priceBN;
    }
  },
  isRoundOver(state) {
    return state.contract.currentRound.endTime - state.now <= 0;
  },
  time(state, getters) {
    const isRoundStarted = state.contract.currentRound.isStarted;
    return getters.isRoundOver || !isRoundStarted ? state.contract.roundDuration : state.contract.currentRound.endTime - state.now;
  },
  isMetaMaskInstalled(state) {
    return state.web3.instance() !== null;
  },
  isMainnet(state) {
    return state.networkType === 'main';
  },
  isBonusUnlocked({ contract, coinbase }, getters) {
    const isBonusExisting = contract.previousRound.totalBonus !== `0` || getters.wasActivePlayer;
    const playedCurrentRound = contract.currentRound.playerClickCount > 0;
    return isBonusExisting && getters.wasPlayer && !getters.isBonusClaimed && (getters.wasActivePlayer || playedCurrentRound);
  },
  isBonusClaimed({ contract }) {
    return contract.previousRound.isBonusClaimed;
  },
  wasPlayer({ contract }) {
    return contract.previousRound.playerClickCount > 0;
  },
  isActivePlayer({ contract, coinbase }) {
    return contract.currentRound.activePlayer.toLowerCase() === coinbase;
  },
  wasActivePlayer({ contract, coinbase }) {
    return contract.previousRound.activePlayer.toLowerCase() === coinbase;
  },
  getTimestamp(state) {
    return (blockNumber) => new Promise((resolve, reject) => {
      // TODO: I'm getting locked out of Infura for hitting it too frequently. I need to change to being notified of block changes.
      state.web3.wsInstance().eth.getBlock(blockNumber, (error, result) => {
        if (error || !result) {
          console.error(`getBlock ${blockNumber} did not return a valid result. Reason:`, error);
          return reject(error);
        } else {
          return resolve(result.timestamp);
        }
      });
    });
  },
  getAvatarColor(state) {
    // Ensure mappedIndex is within range of colorMap's values by %'ing on the size.
    return (addressName) => state.avatarColors[parseInt(addressName, 16) % state.avatarColors.length] || state.defaultAvatarColor;
  },
  getAvatarColorAttribute(state, getters) {
    return (address) => `--avatar-color: ${getters.getAvatarColor(getters.getAddressName(address))}`;
  },
  // Convert value in wei to value in ether for displaying to users
  fromWei(state) {
    return (weiAmount) => parseFloat(utils.fromWei(weiAmount)).toFixed(3);
  },
  getAddressName(state) {
    return (address) => address ? address.substring(2, 4) : ``;
  },
  parseClickEvent(state) {
    // This is a clickEvent log's arguments.
    return ({ returnValues }) => {
      return {
        id: parseInt(returnValues._id),
        price: returnValues._price,
        // Convert from seconds (default Solidity type) to milliseconds (default Date 'time' value)
        endTime: parseInt(returnValues._endTime) * 1000,
        activePlayer: returnValues._activePlayer,
        clickCount: parseInt(returnValues._clickCount),
        totalBonus: returnValues._totalBonus,
        // Since data is coming from a 'click' event the round must be started already
        isStarted: true,
        playerClickCount: returnValues._activePlayer === state.coinbase ? parseInt(returnValues._activePlayerClickCount) : 0,
        previousRoundTotalBonus: returnValues._previousRoundTotalBonus
      };
    };
  },
  getHistoryItem(state) {
    return (paymentEvent) => {
      return {
        price: paymentEvent.returnValues._amount,
        address: paymentEvent.returnValues._recipient,
        txHash: paymentEvent.transactionHash,
        blockNumber: paymentEvent.blockNumber
      };
    };
  }
};



