<template>
  <v-list-tile avatar @click='openEtherScan'>
    <v-list-tile-avatar tile :style='getAvatarColorAttribute(address)'>
        {{ getAddressName(address) }}
    </v-list-tile-avatar>
    <v-list-tile-content>
      <v-list-tile-title>Received {{ fromWei(price) }} Ether</v-list-tile-title>
    </v-list-tile-content>
    <v-list-tile-action>
      <v-list-tile-action-text> {{ elapsedTime }} </v-list-tile-action-text>
    </v-list-tile-action>
  </v-list-tile>
</template>

<script>
import { mapState, mapGetters } from 'vuex';

export default {
  name: 'historyItem',

  props: ['price', 'blockNumber', 'address', 'txHash'],

  data() {
    return {
      blockTimestamp: 0
    };
  },

  computed: {
    ...mapState([
      'now'
    ]),
    ...mapGetters([
      'getTimestamp',
      'getAvatarColorAttribute',
      'getAddressName',
      'fromWei'
    ]),
    // Return a user-friendly string showing how much time has passed since the given blockNumber.
    // Results include: ">99 hr, 1 hr", "2 hrs", "59 mins", "1 min", "<1 min"
    elapsedTime() {
      if (!this.blockTimestamp) return `...`;

      const minutes = Math.floor((this.now - this.blockTimestamp * 1000) / 60000);
      const hours = Math.floor(minutes / 60);
      const cappedHours = hours > 99 ? `>99` : hours;

      return cappedHours ? `${cappedHours} hr` : minutes ? `${minutes} min` : `<1 min`;
    }
  },

  methods: {
    openEtherScan() {
      window.open(`https://etherscan.io/tx/${this.txHash}`);
    }
  },

  async created() {
    // Don't query for timestamp inside of `getElapsedTime` because `getElapsedTime` runs every 500ms.
    // Querying blockchain for data that rapidly results in Infura throttling access.
    this.blockTimestamp = await this.getTimestamp(this.blockNumber);
  }

};
</script>

<style scoped>
</style>


