<template>
  <v-container class='about px-5' grid-list-xl>
    <h1 class='display-2'>
      <p>
        About
      </p>
    </h1>
    
    <v-card raised class='description'>
      <v-card-text>
        <v-layout row wrap align-center>
          <v-flex xs12 md4>
            <v-card class='elevation-0 transparent'>
              <v-card-text class='text-xs-center'>
                <font-awesome-icon :icon='["fal", "mouse-pointer"]' size='4x' class='blue--text text--darken-2'></font-awesome-icon>
              </v-card-text>
              <v-card-title primary-title class='layout justify-center'>
                <div class='display-1 text-xs-center'>You Click</div>
              </v-card-title>
              <v-card-text class='subheading'>
                When you press EtherButton, a transaction is created which sends the listed amount of ETH to the smart contract.
              </v-card-text>
            </v-card>
          </v-flex>
          <v-flex xs12 md4>
            <v-card class='elevation-0 transparent'>
              <v-card-text class='text-xs-center'>
                <font-awesome-icon :icon='["fal", "repeat-1"]' size='4x' class='green--text text--darken-2'></font-awesome-icon>
              </v-card-text>
              <v-card-title primary-title class='layout justify-center'>
                <div class='display-1 text-xs-center'>They Click</div>
              </v-card-title>
              <v-card-text class='subheading'>
                Another player presses EtherButton before the seven hour countdown expires, creating a new transaction.
              </v-card-text>
            </v-card>
          </v-flex>
          <v-flex xs12 md4>
            <v-card class='elevation-0 transparent'>
              <v-card-text class='text-xs-center'>
                <font-awesome-icon :icon='["fal", "star-exclamation"]' size='4x' class='amber--text'></font-awesome-icon>
              </v-card-text>
              <v-card-title primary-title class='layout justify-center'>
                <div class='display-1 text-xs-center'>You Win</div>
              </v-card-title>
              <v-card-text class='subheading'>
                As part of that transaction being processed, you will be refunded your original payment plus 5.00% profit!
              </v-card-text>
            </v-card>
          </v-flex>
          <v-flex xs12 md6>
            <v-card class='elevation-0 transparent'>
              <v-card-title primary-title>
                <div class='display-1 text-xs-center'>Bonus Payouts</div>
              </v-card-title>
              <v-card-text class='subheading'>
                1.00% of all transactions accumulate as end-of-round bonuses. Each click during a round earns an equal share of the total bonus.
              Your share of the bonus can be withdrawn from the contract after your first click in the following round. Any unclaimed bonus ETH
              is rolled forward each round.
              </v-card-text>
            </v-card>
          </v-flex>
          <v-flex xs12 md6>
            <v-card class='elevation-0 transparent'>
              <v-card-title primary-title>
                <div class='display-1 text-xs-center'>Safety Net</div>
              </v-card-title>
              <v-card-text class='subheading'>
                In the event EtherButton is not pressed before the countdown expires, the most recent presser will have 0.500 ETH added to their bonus.
              They will also have their bonus unlocked without requiring participation in the following round. This makes the first click in a new round
              effectively risk free!
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
export default {
  name: 'About'
};
</script>

<style scoped>
.description {
  border-top: 3px solid #6200EA;
}
</style>


