<template>
  <v-card raised :height='height' class='history' :style='style'>
    <v-card-text>
      <v-list subheader>
        <v-subheader>Payout History</v-subheader>
        <template v-for="(item, index) in contract.historyItems">
          <history-item v-bind="item" :key="item.txHash"></history-item>
          <v-divider inset :key="index"></v-divider>
        </template>
      </v-list>
    </v-card-text>
  </v-card>
</template>

<script>
import HistoryItem from './historyItem.vue';
import { mapState } from 'vuex';

export default {
  name: `history`,

  components: {
    HistoryItem
  },

  computed: {
    ...mapState([
      'contract'
    ]),

    height() {
      if (this.$vuetify.breakpoint.xsOnly) {
        return '30vh';
      }

      if (this.$vuetify.breakpoint.mdAndDown) {
        return `35vh`;
      }

      return `50vh`;
    },

    style() {
      return `transform: translateY(${this.$vuetify.breakpoint.mdAndDown ? 5 : 0}vh);`;
    }
  }
};
</script>

<style scoped>
.history {
  border-top: 3px solid #6200EA;
  overflow: hidden;
}
</style>


