<template>
  <v-snackbar bottom right v-model='isVisible'>
    {{ text }}
    <v-btn flat color='pink' @click.native="isVisible = false">Close</v-btn>
  </v-snackbar>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
  name: 'notification',

  data() {
    return {
      isVisible: false,
      text: ''
    };
  },

  computed: {
    ...mapState([
      'snackbar'
    ]),
    messages() {
      return this.snackbar.messages;
    }
  },

  methods: {
    ...mapMutations({
      'dequeueSnackbarMessage': 'DEQUEUE_SNACKBAR_MESSAGE'
    }),

    dequeueMessage() {
      this.text = this.messages[0];
      this.isVisible = true;
      this.dequeueSnackbarMessage();
    }
  },

  watch: {
    async isVisible(isVisible) {
      if (isVisible || !this.messages.length) return;

      await this.$nextTick();
      this.dequeueMessage();
    },

    messages() {
      if (!this.isVisible && this.messages.length) {
        this.dequeueMessage();
      }
    }
  }
};
</script>

<style scoped>
</style>


