export default {
  SET_WEB3(state, { web3, wsWeb3 }) {
    // Wrap the web3 instance in a function to prevent Vue from walking contract's getters because
    // this causes async calls to attempt to run using the current provider.
    state.web3.instance = () => { return web3; };
    state.web3.wsInstance = () => { return wsWeb3; };
  },

  SET_CONTRACT(state, { contract, wsContract, initialPrice, roundDuration, owner }) {
    // Wrap the contract instance in an function to prevent Vue from walking contract's getters because
    // this causes async calls to attempt to run using the current provider.
    state.contract.instance = () => { return contract; };
    state.contract.wsInstance = () => { return wsContract; };

    state.contract.initialPrice = initialPrice;
    state.contract.roundDuration = roundDuration;
    state.contract.owner = owner;
  },

  SET_ROUNDS(state, { currentRound, previousRound }) {
    state.contract.currentRound = currentRound;
    state.contract.previousRound = previousRound;
  },

  SET_NETWORK_TYPE(state, networkType) {
    state.networkType = networkType;
  },

  SET_COINBASE(state, coinbase) {
    state.coinbase = coinbase;
  },

  SET_ROUND_STATE({ contract }, { id, price, endTime, activePlayer, playerClickCount, clickCount, totalBonus, isStarted, previousRoundTotalBonus }) {
    // Advance the round by moving the round pointers forward by one and initializing the newly current round.
    if (contract.currentRound.id !== id) {
      contract.previousRound = contract.currentRound;
      contract.currentRound = {
        id,
        isBonusClaimed: false
      };
    }
    // This is updated because when moving between rounds bonuses roll over from previous previous rounds.
    contract.previousRound.totalBonus = previousRoundTotalBonus;

    // Update the round with any pertinent information. Applies to both new and previously existing rounds.
    Object.assign(contract.currentRound, {
      price,
      endTime,
      activePlayer,
      playerClickCount,
      clickCount,
      totalBonus,
      isStarted
    });
  },

  SET_BALANCE(state, balance) {
    state.balance = balance;
  },

  CLAIM_BONUS({ contract }) {
    contract.previousRound.isBonusClaimed = true;
  },

  DEQUEUE_SNACKBAR_MESSAGE({ snackbar }) {
    snackbar.messages.shift();
  },

  ENQUEUE_SNACKBAR_MESSAGE({ snackbar }, message) {
    snackbar.messages.push(message);
  },

  ADD_HISTORY_ITEM(state, historyItem) {
    if (!state.contract.historyItems.some(item => item.txHash === historyItem.txHash)) {
      state.contract.historyItems.push(historyItem);
      state.contract.historyItems.sort((a, b) => b.blockNumber - a.blockNumber);
    }
  },

  SET_NOW(state) {
    state.now = Date.now();
  },

  SET_EXTRA_CLICK_SAFEGUARD_COUNT(state, extraClickSafeguardCount) {
    state.extraClickSafeguardCount = extraClickSafeguardCount;
  }
};



