<template>
  <v-container class='introduction' fill-height fluid>
    <v-layout v-bind='columnLayoutBinding'>
      <v-flex xs8 lg4 offset-lg4>
        <v-layout wrap align-content-space-around class='text-shadow text-xs-center'>
          <v-flex xs12 :class='fontSize' class='text'>
              {{ fromWei(contract.currentRound.price) }} <font-awesome-icon :icon="['fab', 'ethereum']"></font-awesome-icon>
          </v-flex>
          <v-flex xs10 offset-xs1 sm8 offset-sm2 md6 offset-md3 lg8 offset-lg2>
            <ether-button></ether-button>
            
            <template v-for="(error) in errors">
              <v-alert :key="error" outline color='red darken-2' icon='priority_high' :value='true' :class='$vuetify.breakpoint.xsOnly ? "mt-3" : "mt-5"' class='subheading'>
                <span>
                  {{ error }}
                </span>
              </v-alert>
            </template>

            <!-- Don't display warnings if errors are being shown. Spamming the user with messages isn't helpful. -->
            <template v-if='errors.length === 0' v-for="(warning) in warnings">
              <v-alert :key="warning" outline color='yellow darken-2' icon='priority_high' :class='$vuetify.breakpoint.xsOnly ? "mt-3" : "mt-5"' :value='true' class='subheading'>
                <span>
                  {{ warning }}
                </span>
              </v-alert>
            </template>

            <!-- Prefer using @click over href='' because href='' changes the button styling -->
            <v-btn v-if='!isMetaMaskInstalled && $vuetify.breakpoint.smAndUp' large target='_blank' color='red darken-2' @click='openMetaMaskWindow' class='my-3'>
              Install MetaMask
            </v-btn>
          </v-flex>
          <v-flex xs12 :class='fontSize' class='text'>
            {{ timeRemaining }}
          </v-flex>
        </v-layout>
      </v-flex>
      <v-flex d-flex xs4 lg3 offset-lg1>
        <v-layout align-end>
          <v-flex xs10 offset-xs1 sm8 offset-sm2 lg12 offset-lg0>
            <history></history>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import History from './History.vue';
import EtherButton from './EtherButton.vue';
import { mapState, mapGetters } from 'vuex';

export default {
  name: `introduction`,

  components: {
    EtherButton,
    History
  },

  methods: {
    openMetaMaskWindow() {
      window.open(`https://metamask.io/`);
    }
  },

  computed: {
    ...mapState([
      'coinbase',
      'contract'
    ]),
    ...mapGetters([
      'time',
      'isMetaMaskInstalled',
      'isMainnet',
      'fromWei',
      'warnings',
      'errors'
    ]),
    timeRemaining() {
      const seconds = Math.max(0, this.time / 1000);
      const minutes = Math.floor(seconds / 60);
      const hours = Math.floor(minutes / 60);

      if (hours > 0) {
        return `${hours} Hour${hours === 1 ? '' : 's'}`;
      }

      if (minutes > 0) {
        return `${minutes} Minute${minutes === 1 ? '' : 's'}`;
      }

      return `<1 Minute`;
    },
    columnLayoutBinding() {
      return {
        column: this.$vuetify.breakpoint.mdAndDown
      };
    },
    fontSize() {
      return this.$vuetify.breakpoint.xsOnly ? 'display-1' : 'display-3';
    }
  }

};
</script>

<style scoped>
.introduction {
  height: calc(100vh - 64px);
}

.text {
  /* Bring text above the fixed background display */
  z-index: 1;
}
</style>


