
import shajs from 'sha.js';

// Return a new Round object with default values.
// Wei values (price/totalBonus) are stored as a string due to BigNumber vs BN conflicts in truffle/Web3.
// They can't be numbers because values may exceed 2^32-1 due to being expressed as Wei.
function getDefaultRound() {
  return {
    id: 0,
    price: `0`,
    endTime: 0,
    activePlayer: ``,
    clickCount: 0,
    totalBonus: `0`,
    playerClickCount: 0,
    isBonusClaimed: false,
    isStarted: false
  };
}

export default {
  encryption: shajs('sha256').update(window.location.hostname).digest('hex').toUpperCase(),
  now: Date.now(),
  networkType: ``,
  coinbase: ``,
  balance: `0`,
  extraClickSafeguardCount: 0,
  snackbar: {
    messages: []
  },
  // NOTE: MetaMask InPage Provider does not (yet!) support Web3 1.0 subscriptions.
  // Infura does have support for event subscriptions. So, keep an Infura-provider instance around for some use cases.
  // wsHost: process.env.NODE_ENV === 'production' ? 'wss://ropsten.infura.io/ws/' : 'ws://127.0.0.1:7545',
  wsHost: 'wss://ropsten.infura.io/ws/',
  // IMPORTANT:
  // Vue will invoke an objects getters during data-binding initialization.
  // The default web3 instance has getters which invoke actions when accessed.
  // As such, wrap the instance with our own web3 and provide access to the underlying instance.
  web3: {
    instance: null,
    wsInstance: null
  },
  contract: {
    instance: null,
    // Need to keep a second instance for websockets because MetaMask doesn't support websockets yet.
    wsInstance: null,
    address: ``, // 0xf12a24b4a2025b189e8d50943739477f1df47e03
    owner: ``,
    roundDuration: 0,
    initialPrice: `0`,
    historyItems: [],
    currentRound: getDefaultRound(),
    previousRound: getDefaultRound()
  },
  defaultAvatarColor: '#607D8B',
  avatarColors: Array.from((new Map([
    [ 'red', '#F44336' ],
    [ 'red accent-1', '#FF8A80' ],
    [ 'red accent-2', '#FF5252' ],
    [ 'red accent-3', '#FF1744' ],
    [ 'red accent-4', '#D50000' ],
    [ 'pink', '#E91E63' ],
    [ 'pink accent-1', '#FF80AB' ],
    [ 'pink accent-2', '#FF4081' ],
    [ 'pink accent-3', '#F50057' ],
    [ 'pink accent-4', '#C51162' ],
    [ 'purple', '#9C27B0' ],
    [ 'purple accent-1', '#EA80FC' ],
    [ 'purple accent-2', '#E040FB' ],
    [ 'purple accent-3', '#D500F9' ],
    [ 'purple accent-4', '#AA00FF' ],
    [ 'deep-purple', '#673AB7' ],
    [ 'deep-purple accent-1', '#B388FF' ],
    [ 'deep-purple accent-2', '#7C4DFF' ],
    [ 'deep-purple accent-3', '#651FFF' ],
    [ 'deep-purple accent-4', '#6200EA' ],
    [ 'indigo', '#3F51B5' ],
    [ 'indigo accent-1', '#8C9EFF' ],
    [ 'indigo accent-2', '#536DFE' ],
    [ 'indigo accent-3', '#3D5AFE' ],
    [ 'indigo accent-4', '#304FFE' ],
    [ 'blue', '#2196F3' ],
    [ 'blue accent-1', '#82B1FF' ],
    [ 'blue accent-2', '#448AFF' ],
    [ 'blue accent-3', '#2979FF' ],
    [ 'blue accent-4', '#2962FF' ],
    [ 'light-blue', '#03A9F4' ],
    [ 'light-blue accent-1', '#80D8FF' ],
    [ 'light-blue accent-2', '#40C4FF' ],
    [ 'light-blue accent-3', '#00B0FF' ],
    [ 'light-blue accent-4', '#0091EA' ],
    [ 'cyan', '#00BCD4' ],
    [ 'cyan accent-2', '#18FFFF' ],
    [ 'cyan accent-3', '#00E5FF' ],
    [ 'cyan accent-4', '#00B8D4' ],
    [ 'teal', '#009688' ],
    [ 'teal accent-3', '#1DE9B6' ],
    [ 'teal accent-4', '#00BFA5' ],
    [ 'green', '#4CAF50' ],
    [ 'green accent-3', '#00E676' ],
    [ 'green accent-4', '#00C853' ],
    [ 'light-green', '#8BC34A' ],
    [ 'light-green accent-3', '#76FF03' ],
    [ 'light-green accent-4', '#64DD17' ],
    [ 'lime accent-4', '#AEEA00' ],
    [ 'amber accent-4', '#FFAB00' ],
    [ 'orange', '#FF9800' ],
    [ 'orange accent-2', '#FFAB40' ],
    [ 'orange accent-3', '#FF9100' ],
    [ 'orange accent-4', '#FF6D00' ],
    [ 'deep-orange', '#FF5722' ],
    [ 'deep-orange accent-1', '#FF9E80' ],
    [ 'deep-orange accent-2', '#FF6E40' ],
    [ 'deep-orange accent-3', '#FF3D00' ],
    [ 'deep-orange accent-4', '#DD2C00' ]])).values())
};



