<template>
  <v-container class='contract px-5' grid-list-xl>
    <h1 class='display-2'>
      <p>
        Contract
      </p>
    </h1>

    <v-card raised class='description'>
      <v-card-text>
        <pre class='overflow'>
          <code class='solidity subheading'>{{ text }}</code>
        </pre>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import highlightjs from 'highlight.js/src/highlight.js';
import { definer } from 'highlightjs-solidity';
import etherButton from '../../contracts/EtherButton.sol';

export default {
  name: `contract`,

  data() {
    return {
      text: etherButton.trim()
    };
  },

  beforeCreate() {
    highlightjs.registerLanguage('solidity', definer);
  },

  mounted() {
    highlightjs.highlightBlock(this.$el.querySelector('.solidity'));
  }
};
</script>

<style scoped>
.description {
  border-top: 3px solid #6200EA;
}
</style>

<style>
code.hljs {
  box-shadow: none;
  color: white;
  background: transparent;
}

code.hljs:before {
  content: '';
}

.hljs-keyword,
.hljs-literal {
  color: #2196F3;
}

.hljs-built_in {
  color: #009688;
}

.hljs-number {
  color: #CDDC39;
}

.hljs-class {
  color: #FF9800
}

.hljs-string {
  color: #FF5722;
}

.hljs-comment {
  color: #4CAF50;
  font-style: italic;
}

pre {
  white-space: pre-line;
}
</style>

<style scoped>
.overflow {
  overflow-x: auto;
}
</style>


