import Web3 from 'web3';
import ABIEtherButton from '../../build/contracts/EtherButton.json';

export default {
  getWeb3({ state, commit, dispatch }) {
    return new Promise(async (resolve, reject) => {
      try {
        // window.web3.eth is deprecated. Create a local instance of Web3 tied to MetaMask
        // and access web3.eth through this local instance. This will enforce web3 version integrity.
        const web3 = typeof window.web3 === 'undefined' ? null : new Web3(window.web3.currentProvider);
        const wsWeb3 = new Web3(state.wsHost);

        commit('SET_WEB3', { web3, wsWeb3 });
        resolve();
      } catch (error) {
        console.log('Failed to get Web3. Reason:', error);
        reject(error);
      }
    }).then(async () => {
      // If the MetaMask Web3 provider has been loaded - query it for account details
      if (state.web3.instance() !== null) {
        await dispatch('getNetworkType');
        await dispatch('getAccountDetails');
      }
    });
  },

  async getContract({ state, commit, dispatch }) {
    if (!state.contract.address) return;

    const wsWeb3 = state.web3.wsInstance();
    const wsContract = new wsWeb3.eth.Contract(ABIEtherButton.abi, state.contract.address);

    const initialPrice = await wsContract.methods.INITIAL_PRICE().call();
    // Store time in ms not seconds for better compatibility with Date
    const roundDuration = parseInt(await wsContract.methods.ROUND_DURATION().call()) * 1000;
    const owner = await wsContract.methods.owner().call();

    const web3 = state.web3.instance();
    const contract = web3 === null ? null : new web3.eth.Contract(ABIEtherButton.abi, state.contract.address);

    commit('SET_CONTRACT', { contract, wsContract, initialPrice, roundDuration, owner });
    await dispatch('getRounds');
  },

  // Synchronizes the state of currentRound + previousRound with the state of the contract.
  // TODO: There's a bug where bonus available doesn't take into account the 'roll-over' amount
  // during the period in which nobody has clicked the button yet to start a new round. It's a
  // very small amount and it's in the user's favor.
  async getRounds({ state, commit, getters }) {
    const contract = state.contract.wsInstance();

    // Solidity can't return a Struct. So, it maps the Struct to an array of values.
    // Map this array back to an object to work with it more easily.
    const getRound = async (id) => {
      const roundValues = await contract.methods.Rounds(id).call();
      const playerClickCount = state.coinbase !== `` ? parseInt(await contract.methods.getPlayerClickCount(id, state.coinbase).call()) : 0;
      const isBonusClaimed = state.coinbase ? (await contract.methods.getIsBonusClaimed(id, state.coinbase).call()) : false;
      const isStarted = parseInt(roundValues[0]) === id;
      const price = isStarted ? roundValues[1] : state.contract.initialPrice;
      // Convert from seconds to milliseconds.
      const endTime = isStarted ? parseInt(roundValues[3]) * 1000 : state.contract.roundDuration;

      return {
        id,
        price,
        endTime,
        clickCount: parseInt(roundValues[4]),
        totalBonus: roundValues[5],
        activePlayer: roundValues[7],
        playerClickCount,
        isBonusClaimed,
        isStarted
      };
    };

    // Can't rely on the smart contract to say whether round is over or not.
    // 'now' doesn't update when calling view methods - only when paying.
    const contractRoundId = parseInt(await contract.methods.RoundId().call());
    const tempRound = await getRound(contractRoundId);
    const isRoundOver = tempRound.endTime - state.now <= 0;
    const roundId = isRoundOver ? contractRoundId + 1 : contractRoundId;
    const currentRound = await getRound(roundId);
    const previousRound = await getRound(roundId - 1);

    commit('SET_ROUNDS', { currentRound, previousRound });
  },

  // TODO: state.web3.instance() could be null
  // Returns the network id MetaMask is executing against.
  // 1 for mainnet, 3 for ropsten, 4 for Rinkeby, 5 for Kovan.
  getNetworkType({ state, commit }) {
    return new Promise((resolve, reject) => {
      state.web3.instance().eth.net.getNetworkType((error, networkType) => {
        if (error) {
          console.error(`Failed to get network type. Reason:`, error);
          return reject(error);
        }

        commit('SET_NETWORK_TYPE', networkType);
        resolve();
      });
    });
  },

  // TODO: state.web3.instance() could be null
  async getAccountDetails({ state, commit, dispatch }) {
    return new Promise((resolve, reject) => {
      state.web3.instance().eth.getCoinbase(async (error, response) => {
        if (error) {
          console.error(`Failed to get coinbase. Reason:`, error);
          return reject(error);
        }

        // web3 getCoinbase returns null when MetaMask is not signed in, but I prefer
        // to represent a username as '' by default rather than null.
        const coinbase = response || '';

        var isAccountChanging = state.coinbase !== '' && state.coinbase !== coinbase;
        commit('SET_COINBASE', coinbase);

        if (isAccountChanging) {
          // TODO: Introduce a user module and wipe localStorage when it changes.
          await dispatch('getContract');
        }

        resolve();
      });
    }).then(() => {
      return new Promise((resolve, reject) => {
        if (!state.coinbase) {
          commit('SET_BALANCE', `0`);
          return resolve();
        }

        state.web3.instance().eth.getBalance(state.coinbase, (error, balance) => {
          if (error) {
            console.error(`Failed to get balance. Reason:`, error);
            return reject(error);
          }

          commit('SET_BALANCE', balance);
          resolve();
        });
      });
    });
  },

  async click({ state, commit, getters }) {
    try {
      const balance = new Web3.utils.BN(state.balance);
      const price = getters.extraClickPrice.gt(balance) ? balance : getters.extraClickPrice;

      const contract = state.contract.instance();
      const sendPromiEvent = contract.methods.click().send({ from: state.coinbase, value: price });

      sendPromiEvent.on('error', (error) => {
        console.error(error);
        commit('ENQUEUE_SNACKBAR_MESSAGE', `Transaction failed.`);
      });

      sendPromiEvent.on('transactionHash', (transactionHash) => {
        console.log('transaction hash', transactionHash);
        commit('ENQUEUE_SNACKBAR_MESSAGE', `Processing transaction...`);
      });

      // Await will resolve once the block has been mined and transaction receipt is available.
      const result = await sendPromiEvent;
      const events = result.events;

      console.log('events:', result, events);

      commit('ENQUEUE_SNACKBAR_MESSAGE', `Transaction mined successfully!`);
      commit('SET_ROUND_STATE', getters.parseClickEvent(events.LogClick));

      if (events.LogPlayerPayout) {
        commit('ADD_HISTORY_ITEM', getters.getHistoryItem(events.LogPlayerPayout));
      }

      // TODO: Can't do this very well because the 2 sec polling to refresh
      // account details causes the value to flicker. Waiting on MetaMask to
      // support subscriptions so that I can change from polling to update-per-block
      // at which time I can enable this.
      // Reduce balance by the value sent to the contract.
      // const balanceBN = new Web3.utils.BN(state.balance);
      // const valueBN = new Web3.utils.BN(value);
      // commit('SET_BALANCE', balanceBN.sub(valueBN).toString());
    } catch (error) {
      console.error(`Failed to click. Reason:`, error);
    }
  },

  async claimBonus({ state, commit, getters }) {
    try {
      const contract = state.contract.instance();
      const sendPromiEvent = contract.methods.claimBonus().send({ from: state.coinbase });

      sendPromiEvent.on('error', (error) => {
        commit('ENQUEUE_SNACKBAR_MESSAGE', `Transaction failed.`);
        console.error(error);
      });

      sendPromiEvent.on('transactionHash', (transactionHash) => {
        commit('ENQUEUE_SNACKBAR_MESSAGE', `Processing transaction...`);
      });

      // Await will resolve once the block has been mined and transaction receipt is available.
      const { events } = await sendPromiEvent;

      commit('ENQUEUE_SNACKBAR_MESSAGE', `Transaction mined successfully!`);

      // If the player did not successfully claim their bonus then no event is emitted.
      if (events.LogClaimBonus) {
        commit('CLAIM_BONUS');
      }
    } catch (error) {
      console.error(`Failed to claim bonus. Reason:`, error);
    }
  }
};



